.userprofileall {
    display: flex;
    flex-direction: column;
    justify-content: start;
}

.blk {
    width: 100%;
    height: 170px;

}

.bgblack {
    background: rgb(26, 26, 26);
}

.main {
    width: 100%;
    height: 100%;
}

.datacontainer {
    width: 990px;
    margin: auto;
    border-radius: 5px;
    display: flex;
    position: relative;
    bottom: 50px;
    align-items: center;
    flex-direction: column;
    justify-content: space-around;


}

.userprofileimage {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    position: relative;
    bottom: 50px;
    /* border:10px double #212529; */
}

.fasla {
    height: 0.2px;
    width: 100%;

    background: rgb(95, 95, 95);
}

.anotherdataandposts{
    width: 990px;
    height: 60vh;
    display: flex;
    margin: 0 auto;
    justify-content: space-between;
    margin-top: -110px;
    align-items:center;
    
}



.infocards {

    width: 325px;
    height: 300px;

    display: flex;
    flex-direction: column;



}

.moredata {

    display: flex;
    width: 100%;
    justify-content: space-around;
}

.skillscard {
    width: inherit;
    border-radius: 5px;
    min-height: 150px;
    min-height: 100px;
    max-height: 250px;
    display: flex;
    flex-direction: column;
    padding: 10px;



}

.howpostscard {
    margin-top: 10px;
    width: inherit;
    border-radius: 5px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;

}

.birthday {
    margin-bottom: 20px;
}

.howpostscard p {
    font-size: 20px;
}

.postscard {

    height: 360px;
    overflow-y: visible;

}

.btn-iniego {
    background: rgb(99 102 241);
    color: #fff;
    position: absolute;
    right: 20px;
    top: 20px;
}

.btn-iniego:hover {
    background: rgb(79, 81, 189) !important;
    color: #fff !important;
}

.dropdown-item {
    cursor: pointer;
}





.bio p {
    font-size: 16px;
    font-weight: 500;
}





.skls {
    word-spacing: 290px;
}



@media (max-width:1000px) {
    .datacontainer {
        width: 98%;
        margin: auto;
        border-radius: 5px;
        display: flex;
        position: relative;
        bottom: 50px;
        align-items: center;
        flex-direction: column;
        justify-content: space-around;
    }

    .anotherdataandposts {
        width: 98%;
    }

    .anotherdataandposts {
        width: 98%;
        height: 60vh;
        display: flex;

        margin: 0 auto;
        justify-content: space-between;
        flex-direction: row;
        margin-top: -60px;
        align-items: baseline;
    }

   





}

@media (max-width:860px) {
    .anotherdataandposts {
        width: 98%;
        height: 60vh;
        display: flex;
  
        margin: 0 auto;
        justify-content: space-between;
        flex-direction: row;
        margin-top: -60px;
        align-items: baseline;
    }

    .postscard {

        width: 70%;
        overflow-y: visible;


    }

    .infocards {
      
        width: 95%;
        height: 300px;

        display: flex;
        flex-direction: column;



    }
}
@media (max-width:750px) {
    .datacontainer {
        width: 98%;
        margin: auto;
        border-radius: 5px;
        display: flex;
        position: relative;
        bottom: 50px;
        align-items: center;
        flex-direction: column;
        justify-content: space-around;
    }

    .anotherdataandposts {
        width: 98%;
    }

    .anotherdataandposts {
        width: 98%;
        height: 60vh;
        display: flex;

        margin: 0 auto;
        justify-content: space-between;
        flex-direction: column;
        margin-top: -30px;
        align-items: center;
    }

    .postscard {

        width: 100%;
        overflow-y: visible;


    }

    .infocards {
      
        width: 260px;
        height: 300px;

        display: flex;
        flex-direction: column;



    }

}
@media (max-width:500px) {
    .btn-iniego {
        background: rgb(99 102 241);
        color: #fff;
        position: absolute;

        top: 5px;
        right: 5px;
        width: 70px;
    }

}

@media (max-width:310px) {
    .btn-iniego {
        background: rgb(99 102 241);
        color: #fff;
        position: absolute;
        display: none;
        top: 5px;
        right: 5px;
        width: 70px;
    }

    .infocards {

        width: 255px;
        height: 300px;

        display: flex;
        flex-direction: column;

    }
}