.sidenav {
    position: relative;
    width: 250px;
    height: 100vh;
    z-index: 88;
    transition: 0.3s;
    direction: rtl;
}

.menuu {
    width: 100%;
    height: 500px;

    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.nodee {

    height: 50px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: end;
    transition: 0.1s;
    border-left: 0px solid rgba(255, 255, 255, 0);

}

.nodee:hover {
    border-left: 15px solid rgba(255, 255, 255, 0);
    transition: 0.1s;
    color: rgb(78, 120, 211) !important;

    cursor: pointer;
}

.nodee .fa-solid {
    margin-left: 30px;

}

.menuu .active {
    color: rgb(61, 120, 248) !important;
    border-left: 10px solid rgb(61, 120, 248);
}

.logocontainer {
    height: 57px;
    width: 100%;
    border-bottom: 1px solid #eee;
    
    display: flex;
    justify-content: space-around;
    align-items: center;
    color: rgb(1, 70, 218);

}


.node:hover .fa-solid {
    margin-left: 30px;
    color: rgb(1, 70, 218);
}

.logocontainer h2 {
    font-size: 18px;
    font-weight: 500;
}

.logocontainer .fa-atom {
    font-size: 30px;
    margin-left: 15px;
}

.mel {
    padding: 6px;
    margin-top: 15px;
}

a {
    text-decoration: none;
    color: rgb(110, 110, 110);
}

#snbtg {
    color: rgb(255, 255, 255);
    z-index: 9999999999;
    font-size: 30px;
    position: fixed;
    top: 60px;
    left: 255px;
    transition: 0.2s;
    display: none;
    cursor: pointer;
    border:2px solid #fff;
    padding:2px 4px;
    border-radius:4px;
}
.textlight a{
    color: #fff !important;
}
@media (max-width:800px) {


    .sidenav {
        position: relative;
        width: 250px;
        height: 100vh;
        z-index: 88;
        transition: 0.3s;
        direction: rtl;
        transform: translateX(-100%);
        transition: 0.3s;
    }


    .shw {
        transform: translateX(0%) !important;
    }

    #snbtg {
        transform: rotate(180deg);
        display: block;
        transition: 0.2s;
    }
}

@media (max-width:310px) {
    .sidenav {
        position: relative;
        width: 250px;
        height: 100vh;
        z-index: 88;
        transition: 0.3s;
        direction: rtl;
        transform: translateX(-100%);
        transition: 0.3s;
    }

    .shw {
        transform: translateX(0%) !important;
    }

    .shw #snbtg {
        transform: rotate(180deg);
        transition: 0.2s;
    }
}