.signupformcontainer {

    width: 70%;
    /* max-height: 600px; */
    margin: 100px auto;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    border-radius: 10px;
    padding:20px 25px;
}

.csub{
    position: absolute;

}

.imginput{
    width:180px;
    margin: auto;
}
.signupuserimage{
    width:100px;
    height:100px;
   
    border-radius:10px;
    margin:20px auto;
}
.sigupsubmitbtn{
    width: fit-content;
    margin:20px auto;
    display: flex;
}
@media (max-width:450px) {
    .signupformcontainer {

        width: 95%;
        /* max-height: 600px; */
        margin: 100px auto;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        border-radius: 10px;
        padding:20px 25px;
    }
}