.card {
    width: 500px;
    margin: 30px auto;
    box-shadow: 0px 0px 2px 0px rgb(99 102 241);

}

.card-body {
    max-height: 150px;
    overflow: scroll;
    background: rgb(252, 252, 252);
}

.pui {
    width: 30px;
    height: 30px;
    display: inline !important;
    border-radius: 50%;
    margin-right: 10px;
    margin-left: 10px;
}

.card-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    direction: rtl;
    background: rgb(238, 238, 238);
}

.lftt {
    display: flex;
    justify-content: end;
    align-items: center;
    direction: rtl;

}

.thrd {
    display: flex;
    justify-content: end;
    align-items: center;
    direction: rtl;
}

.crd-dark .card-header {
    background: #2e2e2e;
    color: #fff;
}

.card-footer {
    background: rgb(238, 238, 238);
}

.crd-dark .card-footer {
    background: #2e2e2e;
    border-top: 1px solid #fff;
}
.crd-dark .dropdown-menu{
    background: #2e2e2e;
 
}
.crd-dark .dropdown-item{
    color: #fff;
 
}
.crd-dark .dropdown-item:hover{
    background: rgb(68, 68, 68);
    color: #fff;
 
}



.date {
    font-size: 12px;
}

.fa-ellipsis {
    cursor: pointer;
    padding: 5px;
}

.fa-ellipsis:hover {
    cursor: pointer;
    background: rgb(228, 228, 228);
    padding: 5px;
    border-radius: 5px;
}

.nad {
    height: 30px;
    display: flex;
    flex-direction: column;
}

.date {
    font-size: 9px;
    position: relative;
    left: 30px;
    bottom: 5px;

}

.df {

    display: flex;

    align-items: center;
}

.pui {
    margin-right: 0px;
    border: 1px solid #000;
}



.dfss {
    display: flex;
    justify-content: space-around;
}





.solid {
    background: rgb(209, 209, 209);
    color: red;
    padding: 5px 25px;
    border-radius: 5px;
    transition: 0.2s;
    cursor: pointer;
}







.comment {

    background: rgb(218, 218, 218);
    border-radius: 5px;
    display: flex;
