.addcomment {
    display: flex;
}

.addcomment input {
    width: 90%;
    margin-right: 5px;
    border: none;
    border-radius: 5px;
    background: rgb(255, 255, 255);
    outline: none;
    padding-left: 10px;
}

.crd-dark .aci {
    background: #131212;
    color: #fff;
    padding-left: 10px;
}

.rcc {
    max-height: 180px;
    overflow: scroll;

}

::-webkit-scrollbar {
    width: 0.3rem !important;
    background: rgb(175, 175, 175);

}

::-webkit-scrollbar-thumb {
    background: rgb(0, 65, 150);
    border-radius: 1rem;
}

::-webkit-scrollbar-thumb:hover {
    background: rgb(0, 65, 150);
}

