.dbc {

    position: relative;
    width: calc(100% - 250px);
    left: 250px;
    bottom: 96vh;
    text-align: center;
    height: 96vh;
    overflow-y: scroll;
    margin-top:5px;
}
.dasboard{
    height:100vh;
  overflow-y: hidden;
}
@media (max-width:800px) {


    .dbc {

        position: relative;
        width:100%;
        left: 0px;
        bottom: 92vh;
        text-align: center;
        height: 96vh;
        overflow-y: scroll;
        margin-top:5px;
    }

}