.searchbar {
    width: 97%;
    border: 1px solid rgb(255, 255, 255);
    margin: 10px auto;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.searchbar div {

    width: 5%;
    margin-right: 10px;
    display: inline;
}


.searchbar i {
    color: #fff;
    font-size: 22px;

}
.searchbar i:hover{
    color: rgb(255, 118, 118) !important;
    font-size: 22px;

}

.sin {
    width: 90%;
    padding: 5px;
    background: rgba(255, 255, 255, 0.356);
    border: none;
    outline: none;
    border-radius: 0px;
    color: rgb(0, 0, 0);
}



