header {
    padding: 10px;
    display: flex;
    justify-content: space-between;
    height: 58px;
    position: fixed;top:0;left: 0;right: 0;
    z-index:9999999999;
}

.headeui {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    cursor: pointer;
    border: 1px solid rgb(55 48 163);
}


.aands {

    width: 100px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    margin-right: 10px;

}

.nbbtn {
    font-size: 25px;
    color: rgb(0, 0, 0);
    border: 2px solid rgb(55 48 163);
    padding: 3px 8px;
    border-radius: 5px;

}

.nbbtn:hover {
    background: rgb(231, 231, 231);
    font-size: 25px;
    color: rgb(0, 0, 0);
    border: 2px solid rgb(0, 0, 0);

    border-radius: 5px;

}

.binps input {
    background: #000;
    color: #fff;
    border: none;
    outline: none;
}

.binps input:focus {
    background: #000;
    color: #fff;
    border: none;
    outline: none;
}

.binps textarea {
    background: #000;
    color: #fff;
    border: none;
    outline: none;
}

.binps textarea:focus {
    background: #000;
    color: #fff;
    border: none;
    outline: none;
}

.headeui:active {
    border: 1px solid rgba(255, 255, 255, 0);
    outline: 2px solid #000;
}

.addnewbtn {
    background: rgb(99 102 241) !important;
    z-index: 9999999;
    position: fixed;
    right: 170px;
    top: 10px;

}

.addnewbtn:hover {
    background: rgb(70, 71, 179) !important;
}

.daasdd {
    margin-left: 40px;
}

.lst {

    width: 550px;
    justify-content: space-around;
    display: flex;
    align-items: center;

}


.panaaasc {
    display: flex;
    flex-direction: row;
    /* margin-right:-25px; */
}

.lst a {
    font-size: 18px;
    padding: 3px 25px;

}

.navtextlight a {
    color: #fff;
}

.navtextlight .dropdown-item {
    color: #fff !important;
}

.navtextlight .dropdown-item:hover {
    background: #000;
    color: #fff !important;
}

.navtextlight hr {
    color: rgb(255, 255, 255);
}
.dropdown{
    width:100px;
    /* background: red; */
    display: flex;
    align-items: center;
}
.bg-dark .nbbtn {
    background: rgb(66, 61, 129);
    font-size: 25px;
    color: rgb(255, 255, 255);
    border: 2px solid rgb(181, 177, 235);
    padding: 3px 8px;
    border-radius: 5px;
}

.bg-dark .nbbtn:hover {
    background: rgba(48, 44, 109, 0);
    font-size: 25px;
    color: rgb(255, 255, 255);
    border: 2px solid rgb(255, 255, 255);
    padding: 3px 8px;
    border-radius: 5px;
}

.lst a:hover {
    background: rgb(235, 235, 235);
    border-radius: 5px;
    color: #000;

}

.dpdblst a {
    font-size: 19px;
    padding: 3px 25px;

}

.dpdblst a:hover {
    background: rgb(235, 235, 235);
    border-radius: 5px;
    color: #000;
}

.dpdblst {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    display: flex;
    height: 100%;

}

.dpdb {
    border-top: 1px solid rgb(226, 226, 226);
    position: absolute;
    z-index: 99999999999;
    top: 50px;
    left: 0;
    right: 0;
    height: 120px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    transform: translateY(-190%);
    transition: 0.3s;
    text-align: center;
}

.bg-light .active {
    background: rgb(218, 218, 218);
    border-radius: 5px;
    color: #000;
}

.fa-camera-retro {
    font-size: 28px;
    color: rgb(99 102 241) !important;
}

.slhfd {
    display: none;
}

.fa-bars {
    font-size: 28px;
    margin-top: 5px;
    margin-left: 14px;
    display: none;
}

.as {
    position: fixed;
    right: 50px;
}



.ddunp {
    font-size: 13px;
    text-decoration: underline;
    margin-bottom: -20px;

}


.hlogo {
    width: 50px;
    height: 35px;
    border-radius: 5px;
    border: 2px solid rgb(0, 0, 0);
    cursor: pointer;
}

.bg-dark .hlogo {
    width: 50px;
    height: 35px;
    border-radius: 5px;
    border: 2px solid #fff;
    cursor: pointer;
}


@media (max-width:1000px) {

    .panaaasc {

        width: 200px;
        display: flex;
        flex-direction: row !important;
    }

}



@media (max-width:650px) {
    .lst {

        width: 250px;
        justify-content: space-around;
        display: flex;
        align-items: center;
        display: none;
    }

    .slhfd {
        display: block;
        margin-top: 5px;
        margin-left: 14px;
    }

    .fa-bars {
        font-size: 28px;
        margin-top: 5px;
        margin-left: 14px;
        display: block;
    }



    .dpdbshow {
        border-top: 1px solid rgb(226, 226, 226);
        z-index: 99999999999;
        position: absolute;
        top: -5px;
        left: 0;
        right: 0;
        height: 120px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
        transform: translateY(50%);
        transition: 0.3s;
    }

    .sbet {
        width: 100%;
        display: flex;
        flex-direction: row-reverse;
        justify-content: space-between;
    }

    .displaynone {
        display: none !important;
    }

.alit{
    justify-content:start !important;
}
}



@media (max-width:340px) {
    .username {
        display: none;
    }

    .addnewbtn {
        background: rgb(99 102 241) !important;
        margin-right: 20px;
        position: fixed;
        right: 70px;
        top: 10px;

    }
    .panaaasc{
        margin-right:-45px;
    }
}