.login {

    display: flex;
    align-items: center;
    justify-content: center;
    height: 70vh;

}

form{
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #fff;
    padding: 50px 100px;
    border-radius: 10px;
    box-shadow: 0px 0px 4px 0px #000;
}
#exampleInputPassword1{
      
   margin-bottom:20px;
}
form span {
    font-style: 12px;
    color: red;
    margin-top: 10px;
}



@media (max-width:395px) {
    form {
        display: flex;
        flex-direction: column;
        align-items: center;
        background: #fff;
        padding: 50px 100px;
        border-radius: 10px;
        box-shadow: 0px 0px 4px 0px #000;
        width:95%;
    }
    #exampleInputPassword1{
      
        width:170% !important;
    }
    #exampleInputEmail1{
      
        width:170% !important;
    }

}
@media (max-width:350px) {
    #exampleInputPassword1{
      
        width:220% !important;
    }
    #exampleInputEmail1{
      
        width:220% !important;
    }
}