.editformcontainer {
    width: 500px;
    background: #fff;
    margin: 70px auto;
    padding: 50px;
    border-radius: 10px;
    min-height: 400px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}



.deltbtncontainer {
    width: 500px;

    background: #fff;
    margin: 70px auto;
    padding: 20px;
    border-radius: 10px;
    text-align: center;

}

.hhhhhhh {
    margin-bottom: -100px;
}

.edudimg {
    width: 50px;
    border-radius: 10px;
    margin-bottom: 30px;
    border: 1px solid #000;
    margin-left: 10px;
}

.imginputs {
    display: flex;
    align-items: center;
}

.dfgdfg {
    position: absolute;
}

.gggggg {
    width: 95%;
}

.dltacc {
    width: 95%;
}

.ccont {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
    margin: 10px;
    background: rgba(119, 119, 119, 0.2);
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
    border-radius: 10px;
    border: 1px solid rgba(255, 255, 255, 0.18);
}


.themecontainer {


    display: flex;
    justify-content: space-around;
}

.theme {
    min-height: 200px !important;
}


.dtfc-dark {
    background: #000;

}

.dtfc-dark .editformcontainer {
    background: #212529;
    color: #fff;
}

.dtfc-dark .deltbtncontainer {
    background: #212529;
    color: #fff;
}
.dtfc-dark input{
    background: #000000;
    color: #fff;
    border:none;
    
}
.dtfc-dark input:focus{
    background: #000000;
    color: #fff;
    border:none;
    
}
.dtfc-dark textarea{
    background: #000000;
    color: #fff;
    border:none;

}
.dtfc-dark textarea:focus{
    background: #000000;
    color: #fff;
    border:none;

}

@media (max-width:780px) {
    .themecontainer {
      
        flex-direction: column;
        display: flex;
        justify-content: space-around;
    }
    


}

@media (max-width:550px) {
    
    .editformcontainer {
        width: 100%;
        background: #fff;
        margin: 200px auto;
        padding: 50px;
        border-radius: 10px;
        text-align: center;
    }



    .editformcontainer {
        width: 95%;
        background: #fff;
        margin: 70px auto;
        padding: 50px;
        border-radius: 10px;
        min-height: 400px;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
    }

    .savebtncontainer {
        width: 95%;

        background: #fff;
        margin: 70px auto;
        padding: 20px;
        border-radius: 10px;
        text-align: center;
    }

    .deltbtncontainer {
        width: 95%;

        background: #fff;
        margin: 70px auto;
        padding: 20px;
        border-radius: 10px;
        text-align: center;
    }

}
@media (max-width:350px) {
    .ccont img{
        width:90%;  }
}