.piecard {
    background: #fff;
    width: 330px;
    border-radius: 10px;
    margin:15px 25px;
    box-shadow:0px 0px 4px #000;
    display: inline-block;
    z-index:-1;
}

.piec {
    width: 259px;
    margin: auto;

}

.pieheader {
    border-bottom: 1px solid #eee;
direction: rtl;
    height: 50px;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    justify-content: end;
}

.pieheader h3 {
    margin-left: 30px;
    font-size:18px;
}


.linecrd{
    background: #fff;
    width:95%;
    margin:20px auto;
    border-radius:10px;
    box-shadow:0 0 4px #000;
    display: inline-block;
}

@media (max-width:420px) {
    .piecard {
        background: #fff;
        width: 250px;
        border-radius: 10px;
        margin:15px 25px;
        box-shadow:0px 0px 4px #000;
        display: inline-block;
    }
    .pieheader {
        border-bottom: 1px solid #eee;
    direction: rtl;
        height: 40px;
        margin-bottom: 10px;
        display: flex;
        align-items: center;
        justify-content: end;
    }

}